@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Noto+Serif);
@import url(https://fonts.googleapis.com/css?family=Oswald);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.TopNav {
  background-color: #23b5d3;
}

.BrandingBox {
  display: flex;
  align-items: center;
  flex: 0.7 1;
}

.MenuPanel {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0.3 1;
}

.BrandText {
  font-family: 'Fredoka One', cursive;
  color: white;
  font-size: 20pt;
}

.MenuOption {
  font-family: 'Oswald', sans-serif;
}

.Content {
  display: flex;
  flex: 0.9 1;
  padding: 4pt;
  flex-direction: column;
}

/* .ContentTopPanel {
  display: flex;
  flex: 0.035;
  flex-direction: row;
} */

.ContentTopPanelCard {
  text-align: justify;
  font-family: 'Noto Serif', serif;
}

blockquote {
  font-weight: 100;
  font-size: 1.5rem;
  max-width: 75vw;
  line-height: 1.4;
  position: relative;
  margin: 0;
  padding: 0.5rem;
}

blockquote:before,
blockquote:after {
  position: absolute;
  color: #f1efe6;
  font-size: 8rem;
  width: 4rem;
  height: 4rem;
}
blockquote:before {
  content: '\201C';
  left: -5rem;
  top: -2rem;
}
blockquote:after {
  content: '\201D';
  right: -5rem;
  bottom: 1rem;
}

.ContentBottomPanel {
  text-align: justify;
}

.Showcase {
  padding: 20px;
}

a:link {
  color: #23b5d3;
}

.TechTag {
  color: #f1807d;
  font-size: 12px;
}

.FootView {
  background-color: #f1807d;
  color: white;
  font-family: 'Oswald', sans-serif;
  flex: 0.1 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

