@import url('https://fonts.googleapis.com/css?family=Noto+Serif');

.ContentTopPanelCard {
  text-align: justify;
  font-family: 'Noto Serif', serif;
}

blockquote {
  font-weight: 100;
  font-size: 1.5rem;
  max-width: 75vw;
  line-height: 1.4;
  position: relative;
  margin: 0;
  padding: 0.5rem;
}

blockquote:before,
blockquote:after {
  position: absolute;
  color: #f1efe6;
  font-size: 8rem;
  width: 4rem;
  height: 4rem;
}
blockquote:before {
  content: '“';
  left: -5rem;
  top: -2rem;
}
blockquote:after {
  content: '”';
  right: -5rem;
  bottom: 1rem;
}
